html, body {
  width: 100%;
  height: 100%; }

.app {
  width: 80%;
  height: 100%;
  padding: 30px;
  margin: 0 auto; }
  @media (max-width: 1200px) {
    .app {
      padding: 20px;
      width: 100%; } }
  .app header {
    margin-bottom: 20px; }
    .app header .app-title {
      font-size: 26px;
      margin: 0;
      color: #6600ff;
      font-family: Fira Code;
      font-weight: bold; }
      .app header .app-title span {
        font-size: 10px;
        color: #d8c1fb;
        vertical-align: super;
        margin-left: 2px; }

.dashboard {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%; }
  .dashboard aside {
    flex-basis: 27%;
    flex-grow: 1; }
  .dashboard main {
    flex-basis: 70%;
    margin-left: 20px; }
  @media (max-width: 900px) {
    .dashboard {
      flex-flow: column-reverse; }
      .dashboard aside {
        margin-top: 10px; }
      .dashboard main {
        margin-left: 0; } }

.story-details {
  border: 1px solid #cfd0d1;
  border-radius: 3px;
  padding: 15px;
  background: whitesmoke;
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.2); }
  .story-details__header {
    margin-bottom: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .story-details__controls {
    margin-top: 5px;
    margin-right: 5px; }
    .story-details__controls button.bp3-button + button.bp3-button {
      margin-left: 11px; }
  .story-details__body {
    padding: 0 8px;
    margin-bottom: 12px; }
  .story-details__name {
    display: flex;
    align-items: center;
    padding-left: 8px; }
    .story-details__name [contenteditable] {
      display: inline-block;
      font-family: Fira Code;
      font-size: 20px;
      border: 1px solid transparent;
      width: fit-content;
      max-width: 85%;
      cursor: text;
      margin-left: 8px;
      padding: 0 4px; }
      .story-details__name [contenteditable]:hover, .story-details__name [contenteditable]:focus {
        border-color: #cfd0d1;
        background-color: #fff;
        outline: none; }
    .story-details__name .bp3-tag {
      height: 20px; }
  .story-details__nothing {
    padding-bottom: 20px; }

.story-form .label {
  border: 1px solid transparent;
  outline: none;
  padding: 5px;
  margin: 0 5px; }
  .story-form .label:first-child {
    padding-left: 0;
    margin-left: 0; }
  .story-form .label:hover, .story-form .label:focus {
    border-color: #cfd0d1;
    background-color: #fff; }

.story-form .field {
  width: fit-content; }

.story-form__row {
  display: flex;
  flex-flow: row wrap;
  width: 100%; }
  .story-form__row > * {
    margin-right: 25px; }

.story-form textarea {
  resize: vertical;
  font-family: Fira Code;
  min-height: 100px; }

.story-form .bp3-numeric-input input {
  width: 50px; }

.story-form .bp3-input-group .bp3-icon-calendar + input {
  width: 125px; }

.story-list {
  border: 1px solid #cfd0d1;
  border-radius: 3px;
  background: whitesmoke;
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.2);
  position: relative; }
  .story-list__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 11px 15px;
    border-bottom: 1px solid #cfd0d1; }
    .story-list__header h1 {
      font-weight: normal;
      margin-left: 6px;
      font-size: 14px; }
  .story-list__body {
    height: 300px;
    overflow-y: auto; }
  .story-list__add {
    padding: 2px;
    font-size: 16px;
    cursor: pointer;
    color: #6600ff; }
  .story-list__no-items {
    padding: 19px;
    display: flex;
    justify-content: center;
    align-items: center; }
  .story-list__file-selector {
    position: absolute;
    width: 0; }

.story-list-item {
  border-left: 4px solid transparent;
  font-size: 12px;
  padding: 10px 15px;
  position: relative;
  cursor: pointer; }
  .story-list-item__delete {
    position: absolute;
    top: 12px;
    right: 12px;
    color: #7e7e7e;
    opacity: 0;
    display: none; }
  .story-list-item:hover .story-list-item__delete {
    opacity: 1; }
  .story-list-item.selected {
    border-left-color: #6600ff;
    background-color: lavender; }
    .story-list-item.selected__name {
      font-weight: 600; }
  .story-list-item__items-count {
    font-size: 10px; }

.task-list .task-item + .task-item {
  margin-top: 18px; }

.task-list__header {
  text-align: right;
  margin-bottom: 15px; }

.task-item {
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
  background-color: white;
  border-radius: 3px;
  padding: 8px; }
  .task-item__header {
    padding: 10px;
    cursor: pointer;
    display: flex;
    align-items: center; }
  .task-item__title {
    font-size: 16px;
    font-family: Fira Code; }
  .task-item__tags .bp3-tag {
    font-size: 11px;
    margin-right: 10px; }
  .task-item__body {
    padding: 15px; }

.task-form .label {
  border: 1px solid transparent;
  outline: none;
  padding: 5px;
  margin: 0 5px; }
  .task-form .label:first-child {
    padding-left: 0;
    margin-left: 0; }
  .task-form .label:hover, .task-form .label:focus {
    border-color: #cfd0d1;
    background-color: #fff; }

.task-form .field {
  width: fit-content; }

.task-form__row {
  display: flex;
  flex-flow: row wrap;
  width: 100%; }
  .task-form__row > * {
    margin-right: 25px; }

.task-form textarea {
  resize: vertical;
  font-family: Fira Code;
  min-height: 200px; }

.task-form .bp3-numeric-input input {
  width: 50px; }

.task-form .bp3-input-group .bp3-icon-calendar + input {
  width: 125px; }

.task-form .title .bp3-form-content {
  width: 100%; }

button.bp3-button {
  font-size: 12px; }

span.bp3-tag.bp3-minimal {
  background-color: lavender !important;
  color: #5107bf !important; }

button svg[data-icon*="chevron"] {
  width: 12px; }

.bp3-icon {
  color: #5107bf !important; }

.bp3-datepicker .DayPicker-Day.DayPicker-Day--selected {
  background-color: #6600ff; }
  .bp3-datepicker .DayPicker-Day.DayPicker-Day--selected:hover {
    background-color: #5107bf; }

[class*="bp3-"]:focus {
  outline: none;
  box-shadow: 0 0 0 1px #5107bf; }
